export default{
	data() {
		return {
			verification: '获取验证码',
			disabled:false,
			accounts:'',
			pwd:'',
			pwds:'',
			verify:'',
			username:'',
			parent_id:'',
			referrer:'',
			treaty_show:false,
			treaty:'',
			open_protocol:'',
			checked:false,
		}
	},
	methods:{
		jump(url){
			uni.navigateTo({
				url:url
			})
		},
		obtain(){ //获取验证码
			
			let that = this
			if(!(/^1[3-9][0-9]\d{8,11}$/.test(that.accounts))){
				uni.showToast({
					icon: 'none',
					title: '手机号码格式不正确'
				});
				return;
			}
			if(that.disabled == true) return
			let data = {
				mobile:that.accounts,
				time:Date.parse(new Date())/1000  //时间戳
			}
			console.log(data)
			uni.request({
				url:that.APIconfig.api_root.login.sendPhone,
				method:'POST',
				data,
				success(res) {
					console.log(res)
					let data = res.data 
					console.log(data)
					uni.showToast({
						icon:'none',
						title:data.msg
					})
					if(data.code == 0){
						that.verification = '60s'
						that.disabled = true
						that.timer = setInterval(function(){
							let num = that.verification.split('s')[0]
							num --
							that.verification = num+'s'
						},1000)
					}
					
				}
			})
			
		},
		reg(){ //注册
			let that = this
			console.log(that.checked)
			if(that.open_protocol == 1){
				if(that.checked == false){
					uni.showToast({
						icon:'none',
						title:'请阅读并同意注册须知'	
					})
					return
				}
			}
			if(!that.pwd && !that.parent_id && that.verify && that.username){
				uni.showToast({
					icon:'none',
					title:'请完整填写注册信息！'
				})
				return;
			}
			if(!(/^1[3-9][0-9]\d{8,11}$/.test(that.accounts))){
				uni.showToast({
					icon: 'none',
					title: '手机号码格式不正确'
				});
				return;
			}
			if(that.pwd != that.pwds){
				uni.showToast({
					icon:'none',
					title:'密码不一致！'
				})
				return;
			}
			
			
			let data_list = {
				mobile:that.accounts,
				time:Date.parse(new Date())/1000,//时间戳
				username:that.username,
				parent_id:that.parent_id,
				password:that.pwd,
				repeat_password:that.pwds,
				sms_code:that.verify,
			}
			if(that.referrer != '') data_list.referrer = that.referrer
			uni.request({
				url:that.APIconfig.api_root.login.register,
				method:'POST',
				data:data_list,
				success(res) {
					console.log(res)
					let data = res.data 
					console.log(data)
					uni.showToast({
						icon:'none',
						title:data.msg
					})
					if(data.code == 0){
						setTimeout(function(){
							uni.redirectTo({
							    url: '../login/login'
							});
						},1500)
					}
				}
			})
		}
	},
	onLoad(options) {
		
		if(options.referrer){
			uni.showToast({
				icon:'none',
				title:options.referrer
			})
			this.referrer = options.referrer
		}
	},
	onShow() {
		let that = this
		// uni.request({  //用户须知
		// 	url:this.service.api_root.reg.RegisterAgreement,
		// 	success(res) {
		// 		that.treaty = res.data.data.content.value
		// 		that.open_protocol = res.data.data.status
		// 	}
		// })
	},
	watch:{
		verification(curval,oldval){// 监听定时器的num值
			if(curval == '-1s'){
				clearInterval(this.timer)
				this.verification = '重新获取'
				this.disabled = false
			}
		}
	},
}
